import styled from 'styled-components';
import { Link } from 'react-router-dom'

export const StyledH1 = styled.h1`
	margin-top: 0;
`

export const Wrapper = styled.div`
  width: 100%;
	padding: 2.5em;

	@media (max-width: 600px) {
		padding: 0;
	}
`

export const StyledDiv = styled.div`
	max-width: 480px;
	padding: 2.5em;
	margin: 2.5em auto;
	text-align: center;
	background: #fff;
	border-radius: 4px;
	border: 1px solid #d9e7ea;

	@media (max-width: 600px) {
		padding: 24px;
		border-radius: 0;
	}
`

export const InputWithLabelAbove = styled.input`
	display: block;
	margin: 5px auto 15px;
	padding: 10px;
	background: transparent;
	border: none;
	width: 100%;
	background: #f7f7f7;
	font-size: 16px;
	font-family: inherit;
	border: 1px solid rgba(0, 0, 0, 0.23);
	border-radius: 4px;
	outline: none;

	&:focus {
		border-bottom-color: #3F51B5;
		box-shadow: 0 1px 0 0 #3F51B5
	}

	&:hover {
		border: 1px solid black;
	}
`

export const Input = styled.input`
	display: block;
	margin: 15px auto;
	padding: 10px;
	background: transparent;
	border: none;
	width: 100%;
	background: #f7f7f7;
	font-family: inherit;
	font-size: 20px;
	border: 1px solid #d9e7ea;
	border-radius: 4px;
	outline: none;

	&:focus {
		outline-color: #73777d;
	}
`

export const Textarea = styled.textarea`
	display: block;
	margin: 15px auto;
	padding: 10px;
	background: transparent;
	border: none;
	width: 100%;
	background: #f7f7f7;
	font-family: inherit;
	font-size: 16px;
	border: 1px solid rgba(0, 0, 0, 0.23);
	border-radius: 4px;
	outline: none;
	resize: vertical;

	&:focus {
    border-bottom-color: #3F51B5;
    box-shadow: 0 1px 0 0 #3F51B5
  }

  &:hover {
    border: 1px solid black;
  }
`

export const StyledButton = styled.button`
	font-size: 20px;
	margin-top: 10px;
	padding: 0.5em 1em;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	width: 100%;
	background: #28445b;
	color: #fff;

	&:disabled {
		opacity: 0.75
	}
`

export const StyledLink = styled(Link)`
    text-decoration: none;
`

export const StyledAddressUl = styled.ul`
	position: absolute;
	margin: 0px;
	padding: 0px;
	border: 1px solid #d9e7ea;
	border-top: none;
	border-image: initial;
	list-style-type: none;
	text-align: left;
	overflow-y: auto;
	width: 100%;
	background: white;
`

export const StyledAddressLi = styled.li`
	cursor: pointer;
	padding: 0.5rem;

	&:hover {
		background: black;
		color: white;
	}
`